body {
    margin: 0;
    font-family: 'Creepster', 'Courier New', Courier, monospace;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #000;
    background-image: url('../public/images/halloween-background.gif');
    /* Ajusta esta ruta */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.countdown {
    text-align: center;
    background: rgba(0, 0, 0, 0.8);
    padding: 2rem;
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.timer-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    /* Para que se ajuste en pantallas más pequeñas */
}

.time-box {
    background: rgba(255, 255, 255, 0.1);
    padding: 1rem;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.time-number {
    display: block;
    font-size: 3rem;
    color: #ff6347;
}

.time-label {
    display: block;
    font-size: 1.5rem;
    margin-top: 10px;
    color: #fff;
}

.glitch {
    position: relative;
    text-align: center;
    font-size: 4rem;
    color: #ff6347;
    animation: fire-animation 1s infinite alternate;
}

@keyframes fire-animation {
    0% {
        text-shadow: 0 0 10px rgba(255, 99, 71, 0.8), 0 0 20px rgba(255, 99, 71, 0.8);
    }
    25% {
        text-shadow: 0 0 20px rgba(255, 99, 71, 0.8), 0 0 30px rgba(255, 99, 71, 0.8);
    }
    50% {
        text-shadow: 0 0 30px rgba(255, 99, 71, 0.8), 0 0 40px rgba(255, 99, 71, 0.8);
    }
    75% {
        text-shadow: 0 0 20px rgba(255, 99, 71, 0.8), 0 0 30px rgba(255, 99, 71, 0.8);
    }
    100% {
        text-shadow: 0 0 10px rgba(255, 99, 71, 0.8), 0 0 20px rgba(255, 99, 71, 0.8);
    }
}

@media (max-width: 768px) {
    .glitch {
        font-size: 2.5rem;
    }
    .time-box {
        padding: 0.5rem;
    }
    .time-number {
        font-size: 2rem;
    }
    .time-label {
        font-size: 1rem;
    }
}